"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";

interface BusRequest {
  id: number;
  name: string;
  pickupLocation: string;
  destination: string;
  date: string;
  time: string;
  status: "Pending" | "Approved" | "Rejected";
}

export default function RequestBusPage() {
  const [requests, setRequests] = useState<BusRequest[]>([]);
  const [name, setName] = useState("");
  const [pickupLocation, setPickupLocation] = useState("");
  const [destination, setDestination] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [message, setMessage] = useState("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!name || !pickupLocation || !destination || !date || !time) {
      setMessage("Please fill in all fields.");
      return;
    }
    const newRequest: BusRequest = {
      id: Date.now(),
      name,
      pickupLocation,
      destination,
      date,
      time,
      status: "Pending",
    };
    setRequests([...requests, newRequest]);
    setName("");
    setPickupLocation("");
    setDestination("");
    setDate("");
    setTime("");
    setMessage("Request submitted successfully.");
  };

  return (
    <div className="max-w-3xl mx-auto p-6 bg-white text-black font-sans rounded-md shadow-md">
      <h1 className="text-3xl font-bold mb-6 text-center">Bus Request Form</h1>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <Label htmlFor="name">Name</Label>
          <Input
            id="name"
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="pickupLocation">Pickup Location</Label>
          <Input
            id="pickupLocation"
            type="text"
            value={pickupLocation}
            onChange={(e) => setPickupLocation(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="destination">Destination</Label>
          <Input
            id="destination"
            type="text"
            value={destination}
            onChange={(e) => setDestination(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="date">Date</Label>
          <Input
            id="date"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="time">Time</Label>
          <Input
            id="time"
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        {message && <p className="text-green-600">{message}</p>}
        <Button type="submit" className="w-full">
          Submit Request
        </Button>
      </form>
    </div>
  );
}
